import { getAgencyById } from '@/services/agencies';

type Client = { id: string; email: string; firstName: string; lastName: string; sport: string; agencyEmail: string; agencyId?: string };

const CLIENTS: Client[] = [
  { id: 'ag1-c1', email: 'a1@athletes.test', firstName: 'Ava', lastName: 'Smith', sport: 'Football', agencyEmail: 'agency1@an.test' },
  { id: 'ag1-c2', email: 'a2@athletes.test', firstName: 'Ben', lastName: 'Jones', sport: 'Basketball', agencyEmail: 'agency1@an.test' },
  { id: 'ag2-c1', email: 'b1@athletes.test', firstName: 'Cara', lastName: 'Lee', sport: 'Baseball', agencyEmail: 'agency2@an.test' },
  { id: 'ag3-c1', email: 'c1@athletes.test', firstName: 'Dan', lastName: 'Kim', sport: 'Soccer', agencyEmail: 'agency3@an.test' },
];

export async function listClientsByAgencyEmail(agencyEmail: string) {
  return CLIENTS.filter(c => c.agencyEmail === agencyEmail);
}

export async function listClientsByAgency(agencyId: string) {
  const agency = await getAgencyById(agencyId);
  if (!agency) return [];
  return listClientsByAgencyEmail(agency.email);
}
export async function upsertClient(input: any) { return { id: input.id ?? 'new-id', ...input }; }
export async function getClient(id: string) { return { id, email: 'athlete1@example.com', firstName: 'A1', lastName: 'L1', sport: 'Football' }; }
export async function deleteClient(id: string) { return { ok: true }; }
export async function getClients() { return listClientsByAgency('agency-001'); }


